.container {
    width: 80vw;
    margin: 0 auto;
    padding: 45px 60px;
    border-radius: 40px;
    background-color: #F9F2EF;
}
.learn{
    max-width: 830px;
    margin: 0 auto;
    padding: 60px 90px 40px;
    font-size: 40px;
    background-color: #fff;
    text-transform: uppercase;
    overflow-x: hidden;
}
.header{
    text-transform: uppercase;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    margin-bottom: 60px;
    a{
        display: block;
        text-decoration: none;
    }
}
.title {
    justify-self: center;
    margin: 0;
    text-align: center;
    font-size: 24px;
    font-weight: 400;
}
.buttons {
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border: none;
    &__back, &__go {
        width: 250px;
        max-width: 250px;
        height: 67px;
        font-size: 24px;
        line-height: 67px;
        text-align: center;
        text-transform: uppercase;
        border: none;
        color: #FFF7F7;
        background-color: #78B4EB;
        cursor: pointer;
    }
}
.count{
    text-align: center;
    line-height: 67px;
}
.learnedWords{
    margin-top: 20px;
    text-align: center;
    font-size: 20px;
}
.next {
    animation: next .8s ease-in-out;
}
.prev {
    animation: prev .8s ease-in-out;
}
  
@keyframes next {
    0% {
        transform: translateX(0);
        opacity: 1;
    }
    25% {
        transform: translateX(-100%);
        opacity: 0;
    }
    50% {
        transform: translateX(100%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes prev {
    0% {
        transform: translateX(0);
        opacity: 1;
    }
    25% {
        transform: translateX(100%);
        opacity: 0;
    }
    50% {
        transform: translateX(-100%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@media (max-width: 1155px){
    .buttons {
        &__back, &__go {
            width: 150px;
        }
    }
}
